import { render } from "@/render";
import { createState } from "@/state";
import type { Template, TemplateSchema } from "@/template";

type Factory = (params?: unknown) => unknown;
type Handler = (element: Element) => void;

const _createTagByFactoryName = (factory: Factory) => {
  return factory.name
    .split(/(?=[A-Z])/)
    .join("-")
    .toLowerCase();
};

export const createComponentState =
  ({ type, props, children }: TemplateSchema, handler: Handler) =>
    () => {
      const state = createState(null);

      state.watch((payload) => {
        console.log(payload);
      });
    };
